module.exports={render:function (){var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;
  return _vm._m(0)
},staticRenderFns: [function (){var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;
  return _c('div', {
    staticClass: "wap-page"
  }, [_c('div', {
    staticClass: "txt"
  }, [_vm._v("\n  融资讯APP资讯服务提供者（简称“我们”）深知个人信息对您的重要性，我们将按照法律法规的规定，保护您的个人信息及隐私安全。我们制定本“隐私政策”并特别提示：希望您在使用融资讯APP及相关服务前仔细阅读并理解本隐私政策，以便做出适当的选择。"), _c('br'), _vm._v("\n\n  本隐私政策将帮助您了解："), _c('br'), _vm._v("\n\n  我们会遵循隐私政策收集、使用您的信息，但不会仅因您同意本隐私政策而采用强制捆绑的方式一揽子收集个人信息。"), _c('br'), _vm._v("\n\n  当您使用或开启相关功能或使用服务时，为实现功能、服务所必需，我们会收集、使用相关信息。除非是为实现基本业务功能或根据法律法规要求所必需的必要信息，您均可以拒绝提供且不影响其他功能或服务。我们将在隐私政策中逐项说明哪些是必要信息。"), _c('br'), _vm._v("\n\n  如果您未登录帐号，我们会通过设备对应的标识符信息来保障信息推送的基本功能。如果您登录了账号，我们会根据账号信息实现信息推送。 设备对应的标识符、精确地理位置、相机、相册（存储）权限，均不会默认开启，只有经过您的明示授权才会在为实现特定功能或服务时使用，您也可以撤回授权。"), _c('br'), _vm._v("\n\n  特别需要指出的是，即使经过您的授权，我们获得了这些敏感权限，也不会在相关功能或服务不需要时而收集您的信息。"), _c('br'), _vm._v("\n\n  本隐私政策适用于您通过融资讯APP应用程序、融资讯APP网页、应用程序编程接口（API）方式来访问和使用我们的产品和服务。"), _c('br'), _vm._v("\n\n  下文将帮您详细了解我们如何收集、使用、存储、传输、共享、转让（如适用）与保护个人信息；帮您了解查询、访问、删除、更正、撤回授权个人信息的方式。其中，有关您个人信息权益的条款重要内容我们已用加粗形式提示，请特别关注。"), _c('br'), _vm._v("\n\n  1.我们如何收集和使用个人信息"), _c('br'), _vm._v("\n\n  2.我们如何使用cookie等同类技术"), _c('br'), _vm._v("\n\n  3.我们如何共享、转让、公开披露个人信息"), _c('br'), _vm._v("\n\n  4.我们如何存储个人信息"), _c('br'), _vm._v("\n\n  5.我们如何保护个人信息的安全"), _c('br'), _vm._v("\n\n  6.管理您的个人信息"), _c('br'), _vm._v("\n\n  7.未成年人使用条款"), _c('br'), _vm._v("\n\n  8.隐私政策的修订和通知"), _c('br'), _vm._v("\n\n  9.联系我们\n  ")]), _vm._v(" "), _c('div', {
    staticClass: "tit"
  }, [_vm._v("1.我们如何收集和使用个人信息")]), _vm._v(" "), _c('div', {
    staticClass: "txt"
  }, [_vm._v("\n  我们会按照如下方式收集您在使用服务时主动提供的，以及通过自动化手段收集您在使用功能或接受服务过程中产生的信息："), _c('br'), _vm._v("\n\n  1.1 注册、登录、认证"), _c('br'), _vm._v("\n\n  1.1.1 注册、登录"), _c('br'), _vm._v("\n\n  a. 当您注册、登录融资讯APP及相关服务时，您可以通过手机号创建账号，并且您可以完善相关的网络身份识别信息（头像、昵称），收集这些信息是为了帮助您完成注册。您还可以根据自身需求选择填写简介来完善您的信息。"), _c('br'), _vm._v("\n\n  b. 您也可以使用第三方账号登录并使用，您将授权我们获取您在第三方平台注册的公开信息（头像、昵称以及您授权的其他信息），用于与融资讯APP账号绑定，使您可以直接登录并使用本产品和相关服务。"), _c('br'), _vm._v("\n\n  1.1.2 认证"), _c('br'), _vm._v("\n\n  在您使用身份认证的功能或服务时，根据相关法律法规，您可能需要提供您的真实身份信息（手机号码）以完成实名验证。 上述信息属于个人敏感信息，您可以拒绝提供，但您将可能无法获得相关服务（如评论、发帖），但不影响其他功能与服务的正常使用。"), _c('br'), _vm._v("\n\n  1.2 资讯浏览"), _c('br'), _vm._v("\n\n  a.融资讯APP可能会向您推荐更感兴趣的信息，为实现这一功能，我们可能会收集必要的日志信息。"), _c('br'), _vm._v("\n\n  b.用于信息展示的日志信息包括："), _c('br'), _vm._v("\n\n  您操作、使用的行为信息：点击、订阅、收藏、搜索、浏览、分享 "), _c('br'), _vm._v("\n  您主动提供的信息：点赞、评论、发帖 "), _c('br'), _vm._v("\n  地理位置信息：GPS信息、WLAN接入点、基站等传感器信息"), _c('br'), _vm._v("\n\n  c.GPS地理位置是敏感个人信息，若您拒绝提供，我们将不会根据GPS信息向您推荐信息，且不会影响融资讯APP其他功能的正常使用。"), _c('br'), _vm._v("\n\n  d.我们会基于IP地址对应的城市向您展示、推荐您所在地区的本地相关信息。"), _c('br'), _vm._v("\n\n  e.我们收集、使用的上述信息进行了去标识化处理，数据分析仅对应特定的、无法直接关联您身份的编码，不会与您的真实身份相关联。"), _c('br'), _vm._v("\n\n  1.3 发布与互动"), _c('br'), _vm._v("\n\n  1.3.1 信息发布"), _c('br'), _vm._v("\n\n  a.您评论、发布帖子时，我们将收集您发布的信息，并展示您的昵称、头像、发布内容。"), _c('br'), _vm._v("\n\n  b.您使用上传图片、发布视频功能时，我们会请求您授权相机、相册（存储）权限。您如果拒绝授权提供，将无法使用此功能，但不影响您正常使用融资讯APP的其他功能。"), _c('br'), _vm._v("\n\n  c.用户因使用我们的产品或者服务而被我们收集的信息，例如其他用户发布的信息中可能含有您的部分信息（如：在评论、发布图文、发布视频中涉及到与您相关的信息）。"), _c('br'), _vm._v("\n\n  1.3.2 互动交流"), _c('br'), _vm._v("\n\n  您主动关注您感兴趣的频道，进行浏览、评论、收藏、点赞或分享内容时，我们会收集您关注的账号，并向您展示您关注的频道发布内容。"), _c('br'), _vm._v("\n\n  1.4 搜索"), _c('br'), _vm._v("\n\n  当您使用融资讯APP搜索服务时，我们会收集您的搜索关键字。为了提供高效的搜索服务，部分搜索关键字及服务日志信息会暂时存储在您的本地存储设备之中，并可向您展示搜索结果内容、搜索历史记录。"), _c('br'), _vm._v("\n\n  1.5 二维码扫描"), _c('br'), _vm._v("\n\n  当您使用扫描二维码功能时，我们需要获取系统的相机和录制视频的读取权限，以支持前述功能的提供。我们当且仅当您通过点击二维码扫描入口，或者以其他未来融资讯APP可提供的方式唤起对应搜索功能时才会进行前述的信息收集，若您拒绝我们收集上述信息，请您关闭设备的拍摄照片和录制视频的读取权限，但这也将导致您无法获得相关服务。"), _c('br'), _vm._v("\n\n  1.6 运营与安全保障"), _c('br'), _vm._v("\n\n  1.6.1 运营与安全"), _c('br'), _vm._v("\n\n  我们致力于为您提供安全、可信的产品与使用环境，提供优质、高效、可靠的服务与信息是我们的核心目标。"), _c('br'), _vm._v("\n\n  1.6.2 设备信息与日志信息"), _c('br'), _vm._v("\n\n  a.为了保障软件服务的安全、运营的质量及效率，我们会收集您的硬件型号、操作系统版本号、国际移动设备识别码、唯一设备标识符、网络设备硬件地址、IP 地址、WLAN接入点、软件版本号、网络接入方式、类型、状态、操作、使用、服务日志。"), _c('br'), _vm._v("\n\n  b.为了预防恶意程序、确保运营质量及效率，我们会收集安装的应用信息或正在运行的进程信息、应用程序的总体运行、使用情况与频率、应用崩溃情况、总体安装使用情况、性能数据、应用来源。"), _c('br'), _vm._v("\n\n  c.我们可能使用您的账户信息、设备信息、服务日志信息以及我们关联公司、合作方在获得您授权或依法可以共享的信息，用于判断账户安全、进行身份验证、检测及防范安全事件。"), _c('br'), _vm._v("\n\n  1.7 收集、使用个人信息目的变更"), _c('br'), _vm._v("\n\n  请您了解，随着我们业务的发展，可能会对“融资讯APP”的功能和提供的服务有所调整变化。 原则上，当新功能或服务与我们当前提供的功能或服务相关时，收集与使用的个人信息将与原处理目的具有直接或合理关联。在与原处理目的无直接或合理关联的场景下，我们收集、使用您的个人信息，会再次进行告知，并征得您的同意。"), _c('br'), _vm._v("\n\n  1.8依法豁免征得同意收集和使用的个人信息"), _c('br'), _vm._v("\n\n  请您理解，在下列情形中，根据法律法规及相关国家标准，我们收集和使用您的个人信息无需征得您的授权同意："), _c('br'), _vm._v("\n\n  a.与国家安全、国防安全直接相关的；"), _c('br'), _vm._v("\n\n  b.与公共安全、公共卫生、重大公共利益直接相关的；"), _c('br'), _vm._v("\n\n  c.与犯罪侦查、起诉、审判和判决执行等直接相关的；"), _c('br'), _vm._v("\n\n  d.出于维护个人信息主体或其他个人的生命、财产等重大合法权益但又很难得到本人同意的；"), _c('br'), _vm._v("\n\n  e.所收集的您的个人信息是您自行向社会公众公开的；"), _c('br'), _vm._v("\n\n  f.从合法公开披露的信息中收集的您的个人信息的，如合法的新闻报道、政府信息公开等渠道；"), _c('br'), _vm._v("\n\n  g.根据您的要求签订或履行合同所必需的；"), _c('br'), _vm._v("\n\n  h.用于维护软件及相关服务的安全稳定运行所必需的，例如发现、处置软件及相关服务的故障；"), _c('br'), _vm._v("\n\n  i.为合法的新闻报道所必需的；"), _c('br'), _vm._v("\n\n  j.学术研究机构基于公共利益开展统计或学术研究所必要，且对外提供学术研究或描述的结果时，对结果中所包含的个人信息进行去标识化处理的；"), _c('br'), _vm._v("\n\n  k.法律法规规定的其他情形。"), _c('br'), _vm._v("\n\n  特别提示您注意，如信息无法单独或结合其他信息识别到您的个人身份，其不属于法律意义上您的个人信息；当您的信息可以单独或结合其他信息识别到您的个人身份时或我们将无法与任何特定个人信息建立联系的数据与其他您的个人信息结合使用时，这些信息在结合使用期间，将作为您的个人信息按照本隐私政策处理与保护。\n  ")]), _vm._v(" "), _c('div', {
    staticClass: "tit"
  }, [_vm._v("2.对 Cookie 和同类技术的使用")]), _vm._v(" "), _c('div', {
    staticClass: "txt"
  }, [_vm._v("\n  Cookie 和同类技术是互联网中普遍使用的技术。当您使用融资讯APP及相关服务时，我们可能会使用相关技术向您的设备发送一个或多个 Cookie 或匿名标识符，以收集和存储您访问、使用本产品时的信息。我们承诺，不会将 Cookie 用于本隐私政策所述目的之外的任何其他用途。我们使用 Cookie 和同类技术主要为了实现以下功能或服务："), _c('br'), _vm._v("\n\n  2.1 保障产品与服务的安全、高效运转"), _c('br'), _vm._v("\n\n  我们可能会设置认证与保障安全性的 cookie 或匿名标识符，使我们确认您是否安全登录服务，或者是否遇到盗用、欺诈及其他不法行为。这些技术还会帮助我们改进服务效率，提升登录和响应速度。"), _c('br'), _vm._v("\n\n  2.2 帮助您获得更轻松的访问体验"), _c('br'), _vm._v("\n\n  使用此类技术可以帮助您省去重复您填写个人信息、输入搜索内容的步骤和流程（示例：记录搜索、表单填写）。"), _c('br'), _vm._v("\n\n  2.3 为您推荐、展示、推送您可能感兴趣的内容或账号"), _c('br'), _vm._v("\n\n  a.我们可能会利用 Cookie 和同类技术了解您的偏好和使用习惯，进行数据分析，以改善产品服务、推荐用户感兴趣的信息或功能，并优化您对广告的选择。"), _c('br'), _vm._v("\n\n  b.在融资讯APP的分享页中，我们可能会使用cookie对浏览活动进行记录，用于向您推荐信息和排查崩溃、延迟的相关异常情况以及探索更好的服务方式。"), _c('br'), _vm._v("\n\n  2.4 cookie的清除"), _c('br'), _vm._v("\n\n  大多数浏览器均为用户提供了清除浏览器缓存数据的功能，您可以在浏览器设置功能中进行相应的数据清除操作。如您进行清除，可能因为这些修改，您可能无法使用依赖于Cookie由公司提供的服务或相应功能。\n  ")]), _vm._v(" "), _c('div', {
    staticClass: "tit"
  }, [_vm._v("3.我们如何共享、转让、公开披露个人信息")]), _vm._v(" "), _c('div', {
    staticClass: "txt"
  }, [_vm._v("\n  3.1共享\n\n  3.1.1 共享原则"), _c('br'), _vm._v("\n\n  a.授权同意原则： 向我们的关联方、第三方共享您的个人信息，需经过您的授权同意，除非共享的个人信息是去标识化处理后的信息，且共享第三方无法重新识别此类信息的自然人主体。如果关联方、第三方使用信息的目的超越原授权同意范围，他们需要重新征得您的同意。"), _c('br'), _vm._v("\n\n  b.合法正当与最小必要原则： 向关联方、第三方共享的数据必须具有合法正当目的，且共享的数据以达成目的必要为限。"), _c('br'), _vm._v("\n\n  c.安全审慎原则： 我们将审慎评估关联方、第三方数据使用共享信息的目的，对这些合作方的安全保障能力进行综合评估，并要求其遵循合作法律协议。我们会对合作方获取信息的软件工具开发包（SDK）、应用程序接口（API）进行严格的安全监测，以保护数据安全。"), _c('br'), _vm._v("\n\n  3.1.2 实现功能或服务的共享信息"), _c('br'), _vm._v("\n\n  a.当您在使用融资讯APP中由我们的关联方、第三方提供的功能，或者当软件服务提供商、智能设备提供商、系统服务提供商与我们联合为您提供服务时我们会将实现业务所必需的信息与这些关联方、第三方共享，以向您进行推荐、展示或推送您可能感兴趣的信息，或者推荐、展示更适合您的特定功能、服务或广告。"), _c('br'), _vm._v("\n\n  b.小程序：当您使用小程序时，未经您同意，我们不会向这些开发者、运营者共享您的个人信息。当您使用小程序时，小程序可能会使用您授权的相关系统权限，您可以在小程序中撤回授权。"), _c('br'), _vm._v("\n\n  c.地理位置服务：当您使用地理位置相关服务时，我们会将GPS信息与位置服务提供商（百度地图）进行共享以便可以向您返回位置结果。GPS信息是敏感个人信息，拒绝提供，仅会影响地理位置服务功能，但不影响其他功能的正常使用。"), _c('br'), _vm._v("\n\n  d.支付功能：支付功能由与我们合作的第三方支付机构向您提供服务。第三方支付机构可能需要收集您的姓名、银行卡类型及卡号、有效期及手机号码。银行卡号、有效期及手机号码 是个人敏感信息，这些信息是支付功能所必需的信息，拒绝提供将导致您无法使用该功能，但不影响其他功能的正常使用。"), _c('br'), _vm._v("\n\n  3.1.3 实现广告相关的共享信息"), _c('br'), _vm._v("\n\n  a.广告推送：我们可能与委托我们进行推广和广告投放的合作伙伴共享信息，但我们不会共享用于识别您个人身份的信息（手机号码）， 仅会向这些合作伙伴提供不能识别您个人身份的间接画像标签及去标识化或匿名化后的信息，以帮助其在不识别您个人身份的前提下提升广告有效触达率。"), _c('br'), _vm._v("\n\n  b.广告统计：为维护/改进我们的产品/服务、为您提供更好的内容，我们可能会接入提供广告监测、数据统计、数据分析服务类的指定授权合作伙伴百度、广点通读取您的设备识别号、联网相关信息，用于分析您使用我们的服务情况、优化广告投放。"), _c('br'), _vm._v("\n\n  3.1.4 实现安全与分析统计的共享信息"), _c('br'), _vm._v("\n\n  a.保障使用安全：我们非常重视账号、服务以及网络信息服务的良好环境。为保障您和其他用户的正当合法权益，维护信息网络环境，我们和关联方或服务提供商可能会共享必要的设备及日志信息。"), _c('br'), _vm._v("\n\n  b.分析产品使用情况：为分析我们服务的使用情况，提升用户使用的体验，可能会与关联方或第三方共享产品来源与使用情况（崩溃、闪退、使用时长等）的去标识化数据，这些数据难以与其他信息结合识别您的个人身份。"), _c('br'), _vm._v("\n\n  3.1.5 帮助您参加营销推广活动"), _c('br'), _vm._v("\n\n  当您选择参加我们及我们的关联方或第三方举办的有关营销活动时，可能需要您提供姓名、通信地址、联系方式等信息。这些信息是敏感个人信息， 拒绝提供可能会影响您参加相关活动，但不会影响其他功能。我们可能会将头像、昵称和/或上述信息与关联方或第三方共享，以保障您在联合活动中获得体验一致的服务，或委托第三方及时向您兑现奖励。"), _c('br'), _vm._v("\n\n  3.2 转让"), _c('br'), _vm._v("\n\n  a.我们不会转让您的个人信息给任何其他第三