import { APP_ID, API_URL, APP_UA, APP_KEY, APP_AKEY } from 'config'
var A_MINUTE = 60000
var AN_HOUR = 3600000
var A_DAY = 86400000

var Apm = function(opts){
    //默认参数
    var _ts = this,
        defOpts = {
        };
    this.opts = Object.assign(defOpts, opts);

    this.config = {
        //platform: "",
        platform: (navigator.userAgent.toLowerCase()).match(/iphone|ipod|ipad/ig) ? 'ios' : 'android',
        app: {
            isInApp: _ts.app.isInApp || false, // aipai/Android/paidashi/paidashi/v(857)  or false
            //is: registerApm.app.is,               //true or false
            o : _ts.app.os || null,       //系统
            s : _ts.app.set || null,      //产品线
            p : _ts.app.product || null,  //app名
            v: _ts.app.version  || null   //版本号
        }
    };
    this.tools = {
        getUrlName: function (name) {
            var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)", "i");
            var r = window.location.search.substr(1).match(reg);
            if (r !== null) return unescape(r[2]);
            return null;
        },
        loadJS: function(url, success){
            var domScript = document.createElement('script');
                domScript.src = url;
                success = success || function(){};
            if(navigator.userAgent.indexOf("MSIE")>0){
                domScript.onreadystatechange = function(){
                    if('loaded' === this.readyState || 'complete' === this.readyState){
                        success();
                        this.onload = this.onreadystatechange = null;
                        this.parentNode.removeChild(this);
                    }
                }
            }else{
                domScript.onload = function(){
                    success();
                    this.onload = this.onreadystatechange = null;
                    this.parentNode.removeChild(this);
                }
            }
            document.getElementsByTagName('head')[0].appendChild(domScript);
        },
        //输出数据，过滤key不存在导致报错问题
        returnData: function (data, keyArr) {
            /*
                data 表示对象
                keyArr 表示数组，['key1', 'key2']
                ==> 表示需要返回 data['key1']['key2'] 的值，其中有某个key不存在就返回: ""（空）
            */
            if (typeof keyArr === "object") {
                try {
                    return eval("data" + "." + keyArr.join("."));
                } catch (e) {
                    return "";
                }
            } else {
                try {
                    return data;
                } catch (e) {
                    return "";
                }
            }
        },
        /**
         * [zkopen_web 客户端内打开]
         * @param  [type] url            [description]
         * @param  [type] need_user_info [description]
         * @param  [type] open_type      [web内部打开，safari外部打开（相当于_blank）]
         * @return [type]                 [description]
         * example shareUrl, 'Y', 'web'
         */
        zkopen_web: function(url, needUserInfo, openType) {
          if (!url) return false;
          needUserInfo = needUserInfo == 'Y' ? 'Y' : 'N';
          var cmd = {
            open_type: openType,
            web_url: url,
            need_user_info: needUserInfo
          };
          var cmdStr = JSON.stringify(cmd);
          var cmdEncodeStr = encodeURIComponent(cmdStr);
          return 'zkopenthirdapp://?_zkcmd=open_type&iphone_cmd_arg=' + cmdEncodeStr + '&androidphone_cmd_arg=' + cmdEncodeStr;
        }
        /*function loadScript(src, callback) {
            var script = document.createElement('script'),
                head = document.getElementsByTagName('head')[0];
            script.type = 'text/javascript';
            script.charset = 'UTF-8';
            script.src = src;
            if (script.addEventListener) {
                script.addEventListener('load', function () {
                    callback();
                }, false);
            } else if (script.attachEvent) {
                script.attachEvent('onreadystatechange', function () {
                    var target = window.event.srcElement;
                    if (target.readyState == 'loaded') {
                        callback();
                    }
                });
            }
            head.appendChild(script);
        }

        loadScript('http://m.aipai.com/aipai_dev/mobile/common/dist/weixin/1.0.0/jweixin.js',function(){
            console.log('wenxian', wx);
        });*/
    };
    this.init();
};

