//初始化
Apm.prototype.init = function(){
    var _ts = this;

    window.WinObserver = {
      //订阅
      add: function (callback, opt) {
        this.subscribers[this.subscribers.length] = {
          callback: callback,
          opt: (opt !== 'undefined') ? opt : {}
        };
      },
      //退订
      remove: function (callback) {
        for (var i = 0; i < this.subscribers.length; i++) {
          if (this.subscribers[i].callback === callback) {
            delete (this.subscribers[i]);
          }
        }
      },
      //发布
      publish: function (what, _observer) {
        let data = {}
        for (var i = 0; i < this.subscribers.length; i++) {
          if (this.subscribers[i] && typeof this.subscribers[i].callback === 'function') {
            let observer = (_observer !== 'undefined') ? _observer : {};
            let key = observer.type+'_'+what.type
            // 执行注册的各种回调
            data[key] = this.subscribers[i].callback({param: what, opt: this.subscribers[i].opt, observer: observer});
          }
        }
        return data
      },
      // 将对象o具有观察者功能
      make: function (o) {
        for (var i in this) {
          o[i] = this[i];
          o.subscribers = [];
        }
      }
    };

    //订阅对象
    window.WinSubscribe = {
      WINBDClient: function(data){
        return this.publish(data, {type: 'WINBDClient'});
      }
    }
    //增加订阅观察者模式
    window.WinObserver.make(window.WinSubscribe);
    //客户端调用WINBDClient，发布协议
    window.WINBDClient = function(param){
      let data = window.WinSubscribe.WINBDClient(param);
      let key = 'WINBDClient_'+param.type
      let _data = data[key] ? data[key] : {stat: 0, data: {}}
      return  _data
    }

    //Timeline start 全局订阅
    window.WinSubscribeTL = {
        onPlay: function(data={}){
            return this.publish(data, {type: 'onPlay'});
        },
        onUpdate: function(data={}){
            return this.publish(data, {type: 'onUpdate'});
        }
    }
    window.WinObserver.make(window.WinSubscribeTL);
    //Timeline end 全局订阅

    document.body.style.cssText = 'max-width:900px;margin:0 auto;';
};

//apm.app
Apm.prototype.app = (function() {
    var ua = navigator.userAgent,
        opt = {};
    var reg = ua.match(/aipai\/(.*)\/(.*)\/(.*)\/v\((.*)\)/);
    if(reg){
        opt.isInApp = reg[0];
        opt.os = reg[1];
        opt.set = reg[2];
        opt.product = reg[3];
        opt.version = reg[4];
    }else{
        opt.isInApp = false;
    }

    opt.is = function (arg){
        if(reg){
            return (typeof arg.o !== 'undefined' ? (arg.o === opt.os) : true) &&  (typeof arg.p !=='undefined' ? (arg.p === opt.product) : true) && (typeof arg.s !== 'undefined' ? (arg.s === opt.set) : true) && (typeof arg.v !== 'undefined' ? (opt.version >= arg.v) : true);
        }else{
            return false;
        }
    };
    return opt;
})();

