//全局事件
Apm.prototype.globalEvent = {
    //track varies kinds of events and listeners.
    handlerTraker: {},
    /**
    * addEventListener
    * NOTE: one callback can only be bound to the same event once. Bind a callback twice doesn't
    *  mean it will be called twice when the event fired once.
    * @param {string} evt - the event name to add a listener on.
    */
    add: function (evt, callback) {
        var _ts = this;
        if (!callback) {
            {
                console.error("[vue-render] missing callback arg in globalEvent.addEventListener.");
            }
            return
        }
        var handlers = _ts.handlerTraker[evt];
        if (!handlers) {
            handlers = _ts.handlerTraker[evt] = [];
        }
        var len = handlers.length;
        for (var i = 0; i < len; i++) {
            if (handlers[i] === callback) {
                // this callback is already bound. no need to bind it again.
                return
            }
        }
        handlers.push(callback);
        document.addEventListener(evt, callback);
    },

    /**
    * removeEventListener
    * NOTE: remove all the event handlers for the specified event type.
    * @param {string} evt - the event name to remove a listener from.
    */
    remove: function(evt) {
        var _ts = this,
            handlers = _ts.handlerTraker[evt];
        if (!handlers) {
          // evt handlers has been already removed.
          return
        }
        handlers.forEach(function (cb) { return document.removeEventListener(evt, cb); });
        delete _ts.handlerTraker[evt];
    }
};

//app协议
Apm.prototype.appClient = function(param){
    try {
        var reg = new RegExp(APP_UA, 'ig')
        if (navigator.userAgent.match(reg)) {
            var _param = Object.assign({}, {
                type: '',
                data: {}
            }, param)
            if (typeof _param.data.cbParam == 'undefined') { _param.data.cbParam = {} }
            if (typeof _param.data.cbName == 'undefined') { _param.data.cbName = '' }
            _param = JSON.stringify(_param)
            var data = null
            console.log(_param)
            if(!navigator.userAgent.match(/iphone|ipod/ig)){
                data = window.BDClient.call(_param)
            }else{
                //data = window.webkit.messageHandlers.BDClient.postMessage(_param)
                data = window.prompt(_param)
            }
            var _data = typeof data == 'string' ? JSON.parse(data) : data
            return _data
        }else{
            return {
                stat: 0,
                msg: '不在客户端内'
            }
        }
    } catch (err) {
        return {
            stat: 0,
            msg: '协议执行失败'
        }
    }
}

Apm.prototype.getHasFlagData = function(sign_arg, data){
    if(!sign_arg){ return data; }
    var _time = String( Date.parse(new Date())/1000 ),
        sign_args = sign_arg.split('|'),
        prefix = sign_args[0],
        sign_argStr = sign_args[1],
        sign_argArr = sign_argStr.split(','),
        baseParams = this.getBaseParams(),
        allParams = Object.assign(baseParams, data, {_time: _time}),
        _flag = APP_KEY + prefix;
    sign_argArr.map((key)=>{
      _flag += allParams[key]
    })
    _flag += allParams['_time']

    return Object.assign(data, {
      _flag: (this.md5((this.md5(_flag)).toLowerCase())).toLowerCase(),
      _time: allParams['_time'],
      sign_arg: sign_arg
    })
}


//app检查参数
Apm.prototype.getBaseParams = function(){
    var reg = new RegExp(APP_UA, 'ig');
    var baseParams = {
      _app_id: '',
      _version: '',
      _v: '',
      _brand: '',//apple
      _systype: navigator.userAgent.match(/iphone|ipod/ig) ? 'iphone' : 'androidphone',
      _dev: '', // app or wechat or baidu
      _udid: '',
      _uid: '',
      //_utoken: '',
      _uauthCode: ''
    };
    var pageData = {};
    var cbData = this.appClient({
      type: 'getBaseInfo',
      data: {}
    })
    //app客户端获取
    if(cbData.stat == 1){
      pageData = cbData.data
      baseParams['isApp'] = true;
    }else{
        baseParams['isApp'] = false;
    }
    //页面参数获取
    if(!pageData._uid){
      let paramArr = window.location.search.substr(1).split('&');
      for(let i=0; i<paramArr.length; i++){
        if(paramArr[i].match(/(.+?)=(.+?)/ig)){
          let curArr = paramArr[i].split('=');
          pageData[curArr[0]] = decodeURIComponent(curArr[1]);
        }
      }
    }
    //cookie获取 appBaseInfo
    if(!pageData._uid){
        let cookies = this.opts.cookies;
        let appBaseInfo = JSON.parse(decodeURIComponent(cookies.get('appBaseInfo') || '{}'));
        if(appBaseInfo._uid){
            pageData = appBaseInfo;
        }
    }

    //存储到本地
    /*if(pageData._uid && pageData._udid){
      storage.setItem({ pageData: pageData })
    }else{
      pageData = storage.getItem('pageData') || {}
    }
    if(typeof name != 'undefined'){
      return pageData[name] || ''
    }else{
      return pageData
    }*/
    baseParams = Object.assign(baseParams, pageData)

    // baseParams = Object.assign(baseParams, {
    //   _app_id: pageData._app_id || '',
    //   _version: pageData._version || '',
    //   _v: pageData._v || '',
    //   _systype: pageData._systype || '',
    //   _brand: pageData._brand || '',
    //   _udid: pageData._udid || '',
    //   _uid: pageData._uid || '',
    //   _utoken: pageData._utoken || '',
    //   _uauthCode: pageData._uauthCode || '',
    //   _dev: pageData._dev || ''
    // })
    return baseParams
}


