// 回到顶部
Apm.prototype.initScrollTop = function(){
    var st = null,
        _scTop = document.createElement('span');
    _scTop.setAttribute('class','g_scrollTop hidden');
    _scTop.setAttribute('id','g_scrollTop');

    document.body.appendChild(_scTop);
    var _el = document.getElementById('g_scrollTop');
    window.addEventListener('scroll', function(event){
        clearTimeout(st);
        st = setTimeout(function(){
            if(document.body.scrollTop > window.innerHeight){
                _el.style.display = 'block';
                _el.addEventListener('click', function(event) {
                    _el.style.display = 'none';
                    event.preventDefault();
                    window.scrollTo(0,0);
                });
            }else{
                _el.style.display = 'none';
            }
        },50);
    });
    _el.addEventListener('touchstart', function(event){
        _el.classList.add('cur');
    });
    _el.addEventListener('touchend', function(event){
        _el.classList.remove('cur');
    });
};

Apm.prototype.fetch = function(_opt){
    var _ts = this,
        pageData = _ts.pageData,
        param = "",
        //isCookies = opt.
        opt = Object.assign({
            method: "GET",
            type: "json",
            cookies: true, //Boolean
            timeout: 10000,
            param: null, //{id: 123}
            paramType: 0, // 0表示参数以字符串形式提交比如“wen=12&xx=333”; 1表示参数以对象形式提交比如“{wen:12, xx:33}”
            headers: {},
            base: true,
            init: false // 是否初始化的接口，如果为true，接口失败后会添加蒙层点击会
        }, _opt);
    var baseParams = this.getBaseParams();
    let _baseParams = {};
    //过滤掉空数据
    for(let key in baseParams) {
        if(baseParams[key] !== ''){
            _baseParams[key] = baseParams[key];
        }
    };
    baseParams = _baseParams;

    const search = window.location.search
    const searchObj = {};
    if (search) {
        const searchArr = decodeURIComponent(search.substring(1)).split(/&/);
        searchArr.forEach((item, index) => {
          const itemArr = item.replace(/=/, "&").split(/&/);
          searchObj[itemArr[0]] = itemArr[1];
        });
    }
    //基本参数
    if (opt.base || opt.full_arg || searchObj.full_arg) {
        opt.param = Object.assign({}, {
            _app_id: baseParams._app_id || APP_ID, //应用ID，每个机构的appid不一样
            _version: baseParams._version || '1.0', //接口版本号，用一个小数表示，比如是5.13不是5.1.3
            _v: baseParams._v || '', //客户端版本号，如5.1.3
            _dev: baseParams._dev || 'wap', // 渠道号，baidu_mp，toutiao_mp，weixin_mp, wap
            _systype: navigator.userAgent.match(/iphone|ipod/ig) ? 'iphone' : 'androidphone',//设备类型，值为 iphone，androidphone
            //_secret: 'Y', //标记用户私隐选择了“仅浏览”
            _brand: '',//apple 设备品牌，比如apple，xiaomi，huawei等
            _uid: baseParams._uid || '', //设备的唯一识别码
            _udid: baseParams._udid || '', //账号ID
            //_uauthCode: '', //在header;账号_uauthCode
        }, opt.param);
    }
    //flag
    opt.param = this.getHasFlagData(opt.sign_arg, opt.param);

    //console.log('baseParams===', baseParams);
    if(baseParams._uauthCode){
        opt.headers = Object.assign({}, {'Authorization': APP_AKEY + ' ' + baseParams._uauthCode}, opt.headers);
    }

    // console.log('opt.param', opt.param);
    //默认补上
    /*opt.param = Object.assign({
        os: (_ts.config.platform === "ios") ? 2 : 1,
        versionCode: parseInt(_ts.config.app.v, 10) || 0
    }, opt.param);*/

    //start zaker url 如果页面有登陆就需要补上用户信息: opt.url
    opt.url = (opt.url.indexOf('http')>=0)? `${opt.url}` : `${API_URL}${opt.url}`
    /*var _loginUrl = '',
        _appid = pageData._appid,
        _udid = pageData._udid,
        _uid = pageData._uid,
        _v = pageData._v,
        _version = pageData._version,
        _webcode = pageData._webcode;
    if(pageData._uid && pageData._appid && pageData._udid){
        _loginUrl = '_appid='+_appid+'&_udid='+_udid+'&_uid='+_uid+'&_v='+_v+'&_version='+_version+'&_webcode='+_webcode;
        opt.url = opt.url+((opt.url).indexOf('?')>=0 ? '&' : '?')+_loginUrl;
    }*/
    //end zaker url 如果页面有登陆就需要补上用户信息: opt.url

    if(location.protocol === 'https:'){
        opt.url = (opt.url).replace(/^http:(\/\/)/, "$1");
    }
    if(location.protocol === 'http:'){
        opt.url = (opt.url).replace(/^https:(\/\/)/, "$1");
    }
    if(opt.param !== null){
        for(var key in opt.param){
            if(typeof opt.param[key] !== 'function'){
                param += '&'+key+'='+ encodeURIComponent(opt.param[key]);
            }
        }
        param = param.substring(1);
    }
    if((opt.method).toLowerCase() === "get"){
        if(opt.type === 'jsonp'){
            return fetchJsonp(opt.url+((opt.url).indexOf('?') >=0 ? '&': '?')+param, {
                timeout: opt.timeout,
                method: opt.method,
                headers: opt.headers,
                credentials: opt.cookies ? 'include' : ''
            }).then(function(response){
                return response.json();
            }).then(function(json){
                return json;
            }).catch(function(ex){
                // 网络错误

                // 初始化的接口，发生错误，显示错误页
                if(opt.init){
                    _ts.pageError({
                        callback: function(){
                            window.location.reload()
                        }
                    })
                }
                return {error: true, url: ex};
            });
        }else{
            return axios(opt.url+((opt.url).indexOf('?') >=0 ? '&': '?')+param, {
                method: opt.method,//"get",
                timeout: opt.timeout,
                withCredentials: opt.cookies,
                headers: opt.headers
            }).then(function(response){
                return response.data;
            }).catch(function(ex){
                // 初始化的接口，发生错误，显示错误页
                if(opt.init){
                    _ts.pageError({
                        callback: function(){
                            window.location.reload()
                        }
                    })
                }
                return {error: true, url: ex};
            });
        }
    }else{
        return axios(opt.url, {
            method: opt.method,//"post",
            timeout: opt.timeout,
            data: (opt.paramType == 0) ? param : opt.param,
            withCredentials: opt.cookies,
            headers: opt.headers
        }).then(function(response){
            return response.data;
        }).catch(function(ex){
            // 初始化的接口，发生错误，显示错误页
            if(opt.init){
                _ts.pageError({
                    callback: function(){
                        window.location.reload()
                    }
                })
            }
            return {error: true, url: ex};
        });

        /*return axios.post(opt.url+'?'+param, {
            timeout: opt.timeout,
            withCredentials: opt.cookies //带上cookie
        }).then(function(response){
            return response.data;
        }).catch(function(ex){
            return {error: true, url: ex};
        });*/
    }
};

Apm.prototype.molg = function(data){

    let _ts = this,
        pageData = _ts.pageData,
        _appid = pageData._appid,
        _udid = pageData._udid,
        _uid = pageData._uid,
        _v = pageData._v,
        _version = pageData._version,
        _webcode = pageData._webcode,
        url = '';
    for(let key in data){
        url += '&'+key+'='+ encodeURIComponent(data[key]);
    }
    if(pageData._uid && pageData._appid && pageData._udid){
        //补用户信息
        url += '&_appid='+_appid+'&_udid='+_udid+'&_uid='+_uid+'&_v='+_v+'&_version='+_version+'&_webcode='+_webcode;
    }
    url = url.substring(1);

    //console.log('//stat.myzaker.com/stat_h5.php?'+url);
    (new Image()).src = '//stat.myzaker.com/stat_h5.php?'+url;
}

Apm.prototype.timeSince = function(dateStr, formatStr = 'yyyy-MM-dd hh:mm') {
    dateStr = dateStr + ''
    dateStr *= (dateStr.length === 10 ? 1000 : 1)
    if (!dateStr) {
      return ''
    }
    var date = new Date(dateStr)
    var recorded = date.valueOf()
    var today = new Date().setHours(0, 0, 0, 0)
    var yesterday = today - A_DAY
    var theDayBefore = yesterday - A_DAY
    var now = new Date().valueOf()
    var diffFromRecorded = now - recorded

    if (diffFromRecorded < A_MINUTE) {
      return '刚刚'
    }

    if (diffFromRecorded < AN_HOUR) {
      return parseInt(`${diffFromRecorded / A_MINUTE}`) + '分钟前'
    }

    if (diffFromRecorded < now - today) {
      return parseInt(`${diffFromRecorded / AN_HOUR}`) + '小时前'
    }
    if (diffFromRecorded < now - yesterday) {
      return this.formatTimestamp(dateStr, '昨天 hh:mm')
    }
    if (diffFromRecorded < now - theDayBefore) {
      return this.formatTimestamp(dateStr, '前天 hh:mm')
    }
    return this.formatTimestamp(dateStr, formatStr)
}

Apm.prototype.timeToSec = function(str) {
  if (!str) {
    return 0
  }
  var arr = str.split(':')
  var sec = 0
  var length = arr.length - 1
  for (var i = 0; i <= length; i++) {
    sec = sec + arr[i] * Math.pow(60, length - i)
  }
  return sec
}

Apm.prototype.formatTimestamp = function(dateStr, str) {
  if (!str) str = 'MM-dd'
  dateStr = dateStr + ''
  dateStr *= (dateStr.length === 10 ? 1000 : 1)
  var date = new Date(dateStr)
  var regExArr = ['y+', 'M+', 'd+', 'h+', 'm+', 's+', 'q+', 'S']
  var resultArr = [
    date.getFullYear(),
    date.getMonth() + 1,
    date.getDate(),
    date.getHours(),
    date.getMinutes(),
    date.getSeconds(),
    Math.floor((date.getMonth() + 3) / 3),
    date.getMilliseconds()
  ]
  for (var i = 0; i < regExArr.length; i++) {
    var pattern = regExArr[i]
    var result = resultArr[i]
    var regEx = new RegExp('(' + pattern + ')')
    if (regEx.test(str)) {
      if (pattern === 'y+') {
        str = str.replace(regEx, result)
      } else {
        var replaceStr = result.toString()
        // replaceStr = ('00' + replaceStr).substring(replaceStr.length)
        replaceStr = this.addO(replaceStr)
        str = str.replace(regEx, replaceStr)
      }
    }
  }
  return str
}

Apm.prototype.addO = function(str) {
  str = str + ''
  if (str.length > 1) {
    return str
  }
  var temp = '00' + str
  return temp.substring(str.length)
}

//apm.md5
Apm.prototype.md5 = function(value){
    var hex_chr = "0123456789abcdef",
        rhex = function(num) {
            var str = "";
            for(var j = 0; j <= 3; j++)
                str += hex_chr.charAt((num >> (j * 8 + 4)) & 0x0F) +
                    hex_chr.charAt((num >> (j * 8)) & 0x0F);
            return str;
        },
        str2blks_MD5 = function(str) {
            var nblk = ((str.length + 8) >> 6) + 1;
            var blks = new Array(nblk * 16);
            for(var i = 0; i < nblk * 16; i++) blks[i] = 0;
            for(var i = 0; i < str.length; i++)
                blks[i >> 2] |= str.charCodeAt(i) << ((i % 4) * 8);
            blks[i >> 2] |= 0x80 << ((i % 4) * 8);
            blks[nblk * 16 - 2] = str.length * 8;
            return blks;
        },
        add = function(x, y) {
            var lsw = (x & 0xFFFF) + (y & 0xFFFF);
            var msw = (x >> 16) + (y >> 16) + (lsw >> 16);
            return (msw << 16) | (lsw & 0xFFFF);
        },
        rol = function(num, cnt) {
            return (num << cnt) | (num >>> (32 - cnt));
        },
        cmn = function(q, a, b, x, s, t) {
            return add(rol(add(add(a, q), add(x, t)), s), b);
        },
        ff = function(a, b, c, d, x, s, t) {
            return cmn((b & c) | ((~b) & d), a, b, x, s, t);
        },
        gg = function(a, b, c, d, x, s, t) {
            return cmn((b & d) | (c & (~d)), a, b, x, s, t);
        },
        hh = function(a, b, c, d, x, s, t) {
            return cmn(b ^ c ^ d, a, b, x, s, t);
        },
        ii = function(a, b, c, d, x, s, t) {
            return cmn(c ^ (b | (~d)), a, b, x, s, t);
        },
        MD5 = function(str) {
            var x = str2blks_MD5(str);
            var a =  1732584193;
            var b = -271733879;
            var c = -1732584194;
            var d =  271733878;

            for(var i = 0; i < x.length; i += 16){
                var olda = a;
                var oldb = b;
                var oldc = c;
                var oldd = d;

                a = ff(a, b, c, d, x[i+ 0], 7 , -680876936);
                d = ff(d, a, b, c, x[i+ 1], 12, -389564586);
                c = ff(c, d, a, b, x[i+ 2], 17,  606105819);
                b = ff(b, c, d, a, x[i+ 3], 22, -1044525330);
                a = ff(a, b, c, d, x[i+ 4], 7 , -176418897);
                d = ff(d, a, b, c, x[i+ 5], 12,  1200080426);
                c = ff(c, d, a, b, x[i+ 6], 17, -1473231341);
                b = ff(b, c, d, a, x[i+ 7], 22, -45705983);
                a = ff(a, b, c, d, x[i+ 8], 7 ,  1770035416);
                d = ff(d, a, b, c, x[i+ 9], 12, -1958414417);
                c = ff(c, d, a, b, x[i+10], 17, -42063);
                b = ff(b, c, d, a, x[i+11], 22, -1990404162);
                a = ff(a, b, c, d, x[i+12], 7 ,  1804603682);
                d = ff(d, a, b, c, x[i+13], 12, -40341101);
                c = ff(c, d, a, b, x[i+14], 17, -1502002290);
                b = ff(b, c, d, a, x[i+15], 22,  1236535329);

                a = gg(a, b, c, d, x[i+ 1], 5 , -165796510);
                d = gg(d, a, b, c, x[i+ 6], 9 , -1069501632);
                c = gg(c, d, a, b, x[i+11], 14,  643717713);
                b = gg(b, c, d, a, x[i+ 0], 20, -373897302);
                a = gg(a, b, c, d, x[i+ 5], 5 , -701558691);
                d = gg(d, a, b, c, x[i+10], 9 ,  38016083);
                c = gg(c, d, a, b, x[i+15], 14, -660478335);
                b = gg(b, c, d, a, x[i+ 4], 20, -405537848);
                a = gg(a, b, c, d, x[i+ 9], 5 ,  568446438);
                d = gg(d, a, b, c, x[i+14], 9 , -1019803690);
                c = gg(c, d, a, b, x[i+ 3], 14, -187363961);
                b = gg(b, c, d, a, x[i+ 8], 20,  1163531501);
                a = gg(a, b, c, d, x[i+13], 5 , -1444681467);
                d = gg(d, a, b, c, x[i+ 2], 9 , -51403784);
                c = gg(c, d, a, b, x[i+ 7], 14,  1735328473);
                b = gg(b, c, d, a, x[i+12], 20, -1926607734);

                a = hh(a, b, c, d, x[i+ 5], 4 , -378558);
                d = hh(d, a, b, c, x[i+ 8], 11, -2022574463);
                c = hh(c, d, a, b, x[i+11], 16,  1839030562);
                b = hh(b, c, d, a, x[i+14], 23, -35309556);
                a = hh(a, b, c, d, x[i+ 1], 4 , -1530992060);
                d = hh(d, a, b, c, x[i+ 4], 11,  1272893353);
                c = hh(c, d, a, b, x[i+ 7], 16, -155497632);
                b = hh(b, c, d, a, x[i+10], 23, -1094730640);
                a = hh(a, b, c, d, x[i+13], 4 ,  681279174);
                d = hh(d, a, b, c, x[i+ 0], 11, -358537222);
                c = hh(c, d, a, b, x[i+ 3], 16, -722521979);
                b = hh(b, c, d, a, x[i+ 6], 23,  76029189);
                a = hh(a, b, c, d, x[i+ 9], 4 , -640364487);
                d = hh(d, a, b, c, x[i+12], 11, -421815835);
                c = hh(c, d, a, b, x[i+15], 16,  530742520);
                b = hh(b, c, d, a, x[i+ 2], 23, -995338651);

                a = ii(a, b, c, d, x[i+ 0], 6 , -198630844);
                d = ii(d, a, b, c, x[i+ 7], 10,  1126891415);
                c = ii(c, d, a, b, x[i+14], 15, -1416354905);
                b = ii(b, c, d, a, x[i+ 5], 21, -57434055);
                a = ii(a, b, c, d, x[i+12], 6 ,  1700485571);
                d = ii(d, a, b, c, x[i+ 3], 10, -1894986606);
                c = ii(c, d, a, b, x[i+10], 15, -1051523);
                b = ii(b, c, d, a, x[i+ 1], 21, -2054922799);
                a = ii(a, b, c, d, x[i+ 8], 6 ,  1873313359);
                d = ii(d, a, b, c, x[i+15], 10, -30611744);
                c = ii(c, d, a, b, x[i+ 6], 15, -1560198380);
                b = ii(b, c, d, a, x[i+13], 21,  1309151649);
                a = ii(a, b, c, d, x[i+ 4], 6 , -145523070);
                d = ii(d, a, b, c, x[i+11], 10, -1120210379);
                c = ii(c, d, a, b, x[i+ 2], 15,  718787259);
                b = ii(b, c, d, a, x[i+ 9], 21, -343485551);

                a = add(a, olda);
                b = add(b, oldb);
                c = add(c, oldc);
                d = add(d, oldd);
            }
            return rhex(a) + rhex(b) + rhex(c) + rhex(d);
        };

    return MD5(value);
};

// 页面错误蒙层
Apm.prototype.pageError = function (_opt) {
    if (document.getElementById('apm-page-error')) {
        // 已经存在一个错误页面
        // console.log('已有错误页面')
        return false
    }
    var opt = Object.assign({
        icon: 'data:image/png;base64,',
        message: '页面加载失败，请重试',
        callback: null
    },_opt)

    var wrap=document.createElement("div");
    wrap.id = 'apm-page-error'
    wrap.innerHTML = `<div style="background-color:#fff;position: fixed;top: 0;left: 0;right: 0;bottom: 0;display: flex;flex-direction: column;justify-content: center;align-items: center;">
      <img style="width: 4rem;"
        src="${opt.icon}">
      <p style="font-size: 0.65rem;color: #C2C2C2;line-height: 1.5;margin:0.8rem;">${opt.message}</p>
    </div>`
    document.body.append(wrap)

    // 注册回调事件
    if(opt.callback){
        wrap.addEventListener('click', function(e) {
            opt.callback()
            //如果提供了事件对象，则这是一个非IE浏览器
            if (e && e.stopPropagation) {
                //因此它支持W3C的stopPropagation()方法
                e.stopPropagation()
            } else {
                //否则，我们需要使用IE的方式来取消事件冒泡
                window.event.cancelBubble = true
            }
        })
    }
    wrap = null
    return true
}

// 移除错误页面
Apm.prototype.hidePageError = function(){
    let errorPage = document.getElementById('apm-page-error')
    if(errorPage){
        errorPage.parentNode.removeChild(errorPage)
        return true
    }
    return false
}


